.Burger-Wrapper {
  width: 32px;
  height: 32px;
  position: relative;
  cursor: pointer;
}
.Menu {
  position: fixed;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-direction: column;
      flex-direction: column;
  -webkit-box-sizing: border-box;
          box-sizing: border-box;
  min-height: 100%;
  width: 100vw;
  background-color: white;
  top: 0;
  left: 0;
  z-index: 3;
  -webkit-transform: translateY(-100%);
      -ms-transform: translateY(-100%);
          transform: translateY(-100%);
  -webkit-transition: 400ms;
  -o-transition: 400ms;
  transition: 400ms;
  padding: 15px 10px;
  overflow: scroll;
  font-size: 2rem;
}

.Menu *:nth-child(n+1){
  margin: 3px;
  color: black;
}

.Menu.true {
  -webkit-transform: translateY(0);
      -ms-transform: translateY(0);
          transform: translateY(0);
}

.Burger-Wrapper div{
  background-color: black;
  width: 100%;
  height: 5px;
  position: absolute;
  -webkit-transition: 400ms;
  -o-transition: 400ms;
  transition: 400ms;
}

.Burger-Wrapper div:first-child {
  margin-top: 4px;
}

.Burger-Wrapper div:nth-child(2) {
  margin-top: 13px;
}

.Burger-Wrapper div:nth-child(3) {
  margin-top: 22px;
}

.Burger-Wrapper.true div:first-child {
  -webkit-transform: rotate(45deg);
      -ms-transform: rotate(45deg);
          transform: rotate(45deg);
  margin-top: 50%;
}

.Burger-Wrapper.true div:nth-child(2) {
  display: none;
}

.Burger-Wrapper.true div:nth-child(3) {
  -webkit-transform: rotate(-45deg);
      -ms-transform: rotate(-45deg);
          transform: rotate(-45deg);
  margin-top: 50%;
}


@media (min-width: 992px) {
  .Burger-Wrapper{
    display: none;
  }
  .Menu {
    position: relative;
    height: auto;
    width: auto;
    background-color: inherit;
    -webkit-transform: translate(0);
        -ms-transform: translate(0);
            transform: translate(0);
    -ms-flex-direction: row;
        flex-direction: row;
    padding: 0;
    font-size: 1.2rem;
    /* justify-content: space-around; */
  }
  .Menu *:nth-child(n+1){
    margin: 0px 10px;
  }
  .Menu *:last-child{
    margin-right: 25px;
  }

}



